:root [cds-theme] {
  --clr-link-active-color: var(--cds-alias-typography-link-color);
  --clr-link-color: var(--cds-alias-typography-link-color);
  --clr-link-hover-color: var(--cds-alias-typography-link-color-hover);
  --clr-link-visited-color: var(--cds-alias-typography-link-color-visited);
  --clr-link-visited-color-hover: var(--cds-alias-typography-link-color-visited-hover);
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

@-ms-viewport {
  width: device-width;
}
html {
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

img {
  vertical-align: middle;
}

[role=button] {
  cursor: pointer;
}

a,
area,
button,
[role=button],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

input,
button,
select,
textarea {
  border-radius: 0;
}

input[type=radio]:disabled,
input[type=checkbox]:disabled {
  cursor: not-allowed;
}

input[type=date],
input[type=time],
input[type=datetime-local],
input[type=month] {
  -webkit-appearance: listbox;
}

textarea {
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  padding: 0;
  line-height: inherit;
}

input[type=search] {
  -webkit-appearance: none;
}

[hidden] {
  display: none !important;
}

dl {
  margin-bottom: 0;
  margin-top: 1rem;
}

table {
  border-spacing: 0;
}

a:link {
  color: var(--color-weight-700);
}

a:visited {
  color: var(--color-weight-700);
}
a:visited:hover {
  color: var(--clr-link-visited-color-hover);
}

a:hover {
  color: var(--color-weight-700);
}

a:active {
  color: var(--color-weight-700);
}

