  --clr-table-font-color: var(--cds-alias-typography-color-400);
  --clr-table-margin: var(--cds-global-space-9) 0 0 0;
  --clr-thead-color: var(--cds-alias-typography-color-400);
  --clr-table-smallpadding: var(--cds-global-space-4);
  --clr-table-bottomcellpadding: calc(var(--clr-table-cellpadding) - var(--cds-global-space-1));
  --clr-table-topcellpadding: var(--cds-global-space-6);
  --clr-table-fontsize: var(--cds-alias-typography-secondary-font-size);
