  --clr-table-cellpadding: var(--cds-global-space-6);
  --clr-table-compact-row-height: var(--cds-global-space-9);
  --clr-table-compact-vertical-padding: calc(
    (var(--clr-table-compact-row-height) - var(--clr-table-lineheight)) * 0.5
