  background: var(--clr-calendar-active-focus-cell-background-color);
}
.day-btn.is-disabled {
  color: var(--cds-alias-object-interaction-color-disabled);
  pointer-events: none;
}
.day-btn.is-disabled:hover {
  background: none;
}

.calendar-pickers {
  display: flex;
}

.switcher {
