  width: calc(0.5 * calc(7 * var(--cds-global-space-11) + 2 * var(--cds-global-space-7) + 2 * var(--cds-alias-object-border-width-100)) - var(--cds-global-space-7) - var(--cds-alias-object-border-width-100));
}
.month:hover, .month:focus,
