.clr-accordion-inner-content {
  padding: var(--cds-global-space-9);
}
@media (min-width: 576px) {
  .clr-accordion-inner-content {
    padding: var(--cds-global-space-8) calc(54 * 1rem / var(--cds-global-base));
  }
